.btnMiss {
  margin: -2px;
  padding: 17px 48px;
  background-color: transparent;
  font-weight: 500;
  font-size: 16px;
  line-height: 17px;
  text-align: center;
  color: #DC3E22;
  border: 2px solid var(--red-color);
  transition: all 0.2s ease-in-out;
}


.btnMiss:hover {
  background-color: var(--red-color);
  color: var(--white-color);
}


@media screen  and (max-width: 576px) {

  .btnMiss {
    font-size: 12px;
    line-height: 12px;
    padding: 9px 11px;

  }
}

