.btnPause {
  padding: 19px 50px;
  margin-right: 25px;
  font-weight: 500;
  font-size: 16px;
  line-height: 17px;
  text-align: center;
  color: #FFFFFF;
  background-color: var(--green-color);
  transition: background-color 0.2s ease-in-out;
}

.btnPause:hover {
  background-color: var(--green-hover-color);
}


@media screen  and (max-width: 576px) {

  .btnPause {
    margin-right: 20px;
    font-size: 12px;
    line-height: 12px;
    padding: 11px 16px;

  }
}
