.btnAddTime {
  position: absolute;
  top: 130px;
  right: 85px;
  margin-left: 25px;
  width: 50px;
  height: 50px;
  background-color: #C4C4C4;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btnAddTime:hover {
  background-color: var(--green-hover-color);
}


@media screen and (max-width: 1450px) {
  .btnAddTime {
    position: initial;
    margin: 0;
    order: -1;
  }
}


@media screen and (max-width: 576px) {
  .btnAddTime {
    width: 35px;
    height: 35px;
  }

  .btnAddTime svg {
    width: 14px;
    height: 14px;
  }
}
