.timerTask {
  width: 100%;
  max-width: 733px;
}

.header {
  display: flex;
  padding: 19px 40px;
  align-items: center;
  justify-content: space-between;
  transition: all 0.2 ease-in-out;
}

.headerTask {
  font-weight: 700;
  font-size: 16px;
  line-height: 17px;
  color: #FFFFFF;
}

.numberPomodor {
  font-weight: 400;
  font-size: 16px;
  line-height: 17px;
  color: #FFFFFF;
}




.content {
  position: relative;
  display: flex;
  padding: 70px 0 107px 0;
  flex-direction: column;
  align-items: center;
  background-color: #F4F4F4;
}

.timer {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 200;
  font-size: 150px;
  line-height: 179px;
  color: #333333;
  transition: color 0.2 ease-in-out;
}


.task {
  text-align: center;
  margin-bottom: 32px;
  font-weight: 400;
  font-size: 16px;
  line-height: 17px;
  color: #333333;
}

.numberTask {
  color: #999999;
}

.btnGroup {
  display: flex;
  flex-direction: row;
  align-items: center;
}


@media screen  and (max-width: 1450px) {
  .timer {
    flex-direction: column;

  } 
}

@media screen  and (max-width: 768px) {
  .timer {
    font-size: 119px;
    line-height: 145px;
  } 
}


@media screen  and (max-width: 576px) {
  .timer {
    font-size: 60px;
    line-height: 100px;
  } 

  .content {
  padding: 30px 0 50px 0;
  }
  
  .task {
    margin-bottom: 20px;
  }

  .header {
    padding: 10px 15px;
  }

  .headerTask {
    margin-right: 20px;
    font-size: 14px;
    line-height: 16px;
  }

  .numberPomodor {
    font-size: 14px;
    line-height: 16px;
  }
}
